label {
  display: block;
}
.App {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-between;
}

.Header {
  color: aqua;
  text-align: center;
  text-decoration: aqua underline;
}

.Search {
  align-self: center;
  margin: 50px auto;
}

li {
  list-style: none;
  margin: 10px auto;
  width: 250px;
}

label {
  width: 100px;
  margin: 10px;
  color: blueviolet;
  display: inline-block;
}

input {
  display: inline-block;
  margin: 10px;
  padding: 3px;
  outline: cornflowerblue outset 2px;
  width: 200px;

}

textarea {
  display: block;
  margin: 10px;
  padding: 3px;
  outline: cornflowerblue inset 2px;
  width: 200px;
height: 200px;
}

button {
  margin: 10px 45%;
  background-color: azure;
  width: 80px;
  font-size: 21px;
  cursor: pointer;
  border-radius: 10px;
  border: darkslategrey 1px solid;
}

button:hover {
  background-color: deeppink;
  color: darkturquoise;
  border: whitesmoke 1px solid;
}

.edit {
  margin: 4px 10px;
  background-color: greenyellow;
  color: green;
  width: 70px;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
  border: darkslategrey 1px solid;
}
.edit:hover {
  background-color: forestgreen;
  color: lawngreen;
}
.del {
  margin: 4px 10px;
  background-color: darkred;
  color: white;
  width: 70px;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
  border: darkslategrey 1px solid;
}
.del:hover {
  background-color: azure;
  color: red;}

.search{
  margin-left: 45%;
  width: 250px;
  background-color: aliceblue;
  outline: darkslategrey 1px solid;
}


h4 {
color: deeppink;
text-align: center;
}

p {
  color: darkslategrey;
  font-size: 18px;
  text-align: center;
}

span {
  display: block;
  font-weight: bolder;
  color: blueviolet;
  font-style: italic;
  text-align: right;
}

.edit {
  display: none;
}